.navbar {
    width: 100%;
    background-color: rgb(49, 36, 37);
    display: flex;
    justify-content: space-between;
    color: rgb(255, 255, 255);
    align-items: center;
    position: absolute;
    top: 0px;
}

.navbar .parentNav {
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.navText {
    font: 10px;
    padding : 5px;
}